import validator from 'validator';
import { validateReservation } from './reservationHelper';

export const validateCustomReservation = (reservation) => {
    
    const errors = validateReservation(reservation);
    
    const {price, commission} = reservation;    

    if (price && !validator.isNumeric(price)) errors.price = 'El precio no es vlido';
    
    if(!commission || !validator.isNumeric(commission)) errors.commission = 'La comisin es requerida y debe ser numrica';

    return errors

    
}
